<template>
  <div>
    <fTitle>
      <template #title>Pagination 分页</template>
      <template #introduce>当数据量过多时，使用分页分解数据。</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法demo</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
    <div class="grid-content">
      <h2>{{ imgList[currentPage-1].id }}</h2>
      <span>{{ imgList[currentPage-1].url }}</span>
    </div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
      <el-col :span="20">
        <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="100"
      background
      layout="prev, pager, next, jumper"
      :total="600">
    </el-pagination>
      </el-col>
    </el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      currentPage: 1,
      imgList: [
        {
          url: '图片1',
          id: 1
        },
        {
          url: '图片2',
          id: 2
        },
        {
          url: '图片3',
          id: 3
        },
        {
          url: '图片4',
          id: 4
        },
        {
          url: '图片5',
          id: 5
        },
        {
          url: '图片6',
          id: 6
        }
      ]
    }
  },
  methods: {
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: 200px;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        border: 3px dashed pink;
      }
    }}
</style>
